  <style scoped>
  .lyrics-container {
    font-family: "Courier New", Courier, monospace;
    line-height: 1.5;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  .play-music-button {
    margin-bottom: 20px;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: 600;
    background: linear-gradient(135deg, #4a90e2 0%, #2b6cd4 100%);
    color: white;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    box-shadow: 0 4px 15px rgba(43, 108, 212, 0.3);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
  }
  .play-music-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(43, 108, 212, 0.4);
  }

  .play-music-button:active {
    transform: translateY(1px);
  }
  .lyrics-content {
    width: 100%;
    max-width: 800px;
  }

  .row {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }

  .notation-image {
    width: 245px;
    max-width:  800px;
    margin-bottom: 10px;
    position: relative;
    left: 0;
  }

  .text {
    position: relative;
    letter-spacing: 0.5em;
    word-spacing: -0.4em;
    margin-left: 50px;
    display: flex;
    justify-content: center;
    white-space: pre;
    margin: 0;
  }

  .char {
    display: inline-block;
    min-width: 1em;
    position: relative;
    transition: transform 0.3s;
    color: #333;

  }
  .play-music-button::after {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(
      45deg,
      transparent 25%,
      rgba(255, 255, 255, 0.1) 50%,
      transparent 75%
    );
    animation: shine 3s infinite;
  }
  .instrument-boxes {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }

  .instrument-box {
    width: 6px; /*  */
    height: 6px; /*  */
    border: 1px solid #ccc;
    margin: 0 3px; /*  */
    display: flex;
    background: rgba(255, 255, 255, 0.9);
    transition: all 0.2s ease;
    box-shadow: 0 2px 8px rgba(74, 144, 226, 0.15);

    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: absolute;  /*  */
  }
  .instrument-box:hover {
    background: #4a90e2;
    transform: scale(1.2);
  }
  .instrument-image {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }

  .instrument-nav {
    position: fixed;
    top: 0;
    right: -100px; /*  */
    width: 100px; /*  */
    height: 100vh;
    background-color: #f4f4f4;
    transition: right 0.3s;
    padding: 10px; /*  */
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column; /*  */
    align-items: center; /*  */
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: -5px 0 25px rgba(0, 0, 0, 0.08);
  }

  .nav-open {
    right: 0;
  }

  .nav-closed {
    right: -100px; /*  */
  }

  .nav-toggle {
    background: #ffffff;
    color: #4a90e2;
    border: 2px solid #4a90e2;
    border-radius: 15px;
    font-weight: 500;
    transition: all 0.3s ease;
    box-shadow: 0 2px 8px rgba(74, 144, 226, 0.2);
    position: absolute;
    top: 10px;
    left: -35px;  /*  */
    padding: 5px 8px;  /*  */
    font-size: 12px;  /*  */
    cursor: pointer;
    white-space: nowrap;  /*  */
  }
  .nav-toggle:hover {
    background: #4a90e2;
    color: white;
    transform: translateX(5px);
  }
  .instrument-list {
    margin-top: 40px;
    display: flex;
    flex-direction: column; /*  */
    align-items: center; /*  */
    max-height: calc(100vh - 60px); /*  */
    overflow-y: auto; /*  */
  }

  .instrument-item {
    width: 40px;
    height: 40px;
    border-radius: 12px;
