<template>
  <div id="app">
    <button
      class="audio-control"
      @click="toggleAudio"
      :class="{ 'is-playing': isAudioPlaying }"
    >
      {{ isAudioPlaying ? '' : '' }}
    </button>
    <!--  -->
    <div v-if="!showVideo" class="dialog-container">
      <h1>AI</h1>
      <input
        type="text"
        v-model="inputText"
        placeholder="..."
        class="input-field"
      />
      <button @click="generateVideo" class="generate-button"></button>

    </div>

    <!--  -->
    <div v-if="showVideo" class="video-container">
      <video controls loop  autoplay class="generated-video">
        <source src="xiaochuan.mp4" type="video/mp4">
        
      </video>
      <button @click="showVideo = false" class="return-button"></button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      inputText: '',
      showVideo: false,
      isAudioPlaying: false,
      audioElement: null
    }
  },
  mounted () {
    // 
    this.audioElement = new Audio('xc1.mp3') // 
    this.audioElement.loop = true // 
  },
  methods: {
    generateVideo () {
      // 
      this.showVideo = true
    },
    toggleAudio () {
      this.isAudioPlaying = !this.isAudioPlaying
      if (this.isAudioPlaying) {
        this.audioElement.play()
      } else {
        this.audioElement.pause()
      }
    }
  },
  beforeDestroy () {
    // 
    if (this.audioElement) {
      this.audioElement.pause()
      this.audioElement = null
    }
  }

}
</script>

<style scoped>
.audio-control {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid #007bff;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.audio-control:hover {
  transform: scale(1.1);
  background: #007bff;
  color: white;
}

.audio-control.is-playing {
  background: #007bff;
  color: white;
}
.video-container {
  max-width: 200px;
  margin: 20px auto;
  text-align: center;
  animation: fadeIn 0.5s ease;
}

.generated-video {
  width: 100%;
  max-width: 600px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.2);
}

.return-button {
  display: block;
  margin: 20px auto;
  padding: 10px 25px;
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.return-button:hover {
  transform: scale(1.05);
  background: #45a049;
}

/* / */
.dialog-container {
  transition: all 0.5s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

body {
  background-color: #f8f9fa; /*  */
  font-family: Arial, sans-serif; /*  */
}

#app {
  max-width: 600px;
  margin: 50px auto; /*  */
  padding: 20px; /*  */
  background-color: #fff; /*  */
  border-radius: 10px; /*  */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /*  */
  text-align: center;
}

h1 {
  color: #333; /*  */
  margin-bottom: 20px; /*  */
}

.input-field {
  width: 80%;
  padding: 15px; /*  */
  margin-bottom: 20px; /*  */
  border: 1px solid #ced4da; /*  */
  border-radius: 5px; /*  */
  transition: border-color 0.3s; /*  */
}

.input-field:focus {
  border-color: #007bff; /*  */
  outline: none; /*  */
}

.generate-button {
  padding: 10px 20px; /*  */
  background-color: #007bff; /*  */
  color: white; /*  */
  border: none; /*  */
  border-radius: 5px; /*  */
  cursor: pointer; /*  */
  transition: background-color 0.3s, transform 0.3s; /*  */
}

.generate-button:hover {
  background-color: #0056b3; /*  */
  transform: scale(1.05); /*  */
}
/*  */
.magic-portal-enter-active,
.magic-portal-leave-active {
  transition: all 1s ease;
  position: absolute;
  width: 100%;
}

.magic-portal-enter-from {
  transform: translateX(100%) rotate(360deg);
  opacity: 0;
}

.magic-portal-leave-to {
  transform: translateX(-100%) rotate(-360deg);
  opacity: 0;
}

/*  */
.magic-jump-button {
  /*  */
  /*  */
  transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.magic-jump-button:hover {
  transform:
    translateY(-5px)
    rotate(2deg)
    scale(1.05);
  filter: hue-rotate(45deg);
}

/*  */
.portal-effects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at var(--x) var(--y),
    rgba(255,255,255,0.3) 10%,
    transparent 30%
  );
  pointer-events: none;
}
</style>
