<template>
    <div class="detail-container">
      <h2>{{ provinceName }} </h2>
      <button @click="$router.go(-1)"></button>
      <!--  -->
      <div v-if="provinceData">
        <p>{{ provinceData.area }} </p>
        <p>{{ provinceData.population }} </p>
        <!-- ... -->
      </div>
    </div>
  </template>

<script>
// 
const mockData = {
  : {
    area: 16410,
    population: 2189
  },
  : {
    area: 179700,
    population: 12601
  }
  // ...
}

export default {
  props: ['name'],
  data () {
    return {
      provinceData: null
    }
  },
  computed: {
    provinceName () {
      return this.name || ''
    }
  },
  created () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      // API
      this.provinceData = mockData[this.provinceName] || null
    }
  }
}
</script>

  <style scoped>
  .detail-container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  </style>
