<template>
    <div class="phone-frame">
      <!--  -->
      <div class="status-bar">
        <div class="time">9:41</div>
        <div class="status-icons">
          <span class="signal"></span>
          <span class="battery"> 100%</span>
        </div>
      </div>

      <div class="screen-content">
        <div v-if="!isConnected" class="wechat-calling-screen">
          <div class="caller-info">
            <img :src="avatarUrl" alt="Avatar" class="wechat-avatar">
            <h2 class="caller-name"></h2>
            <p class="call-status"></p>
          </div>

          <div class="wechat-button-container">
            <button @click="rejectCall" class="wechat-reject-button">
              <span class="icon"></span>
              <span class="label"></span>
            </button>
            <button @click="acceptCall" class="wechat-accept-button">
              <span class="icon"></span>
              <span class="label"></span>
            </button>
          </div>
        </div>

        <div v-else class="wechat-connected-screen">
          <video ref="videoElement" :src="videoUrl" autoplay class="wechat-video-element"></video>
          <div class="control-bar">
            <button class="control-button">
              <span class="icon"></span>
            </button>
            <button @click="hangupCall" class="hangup-button">
              <span class="icon"></span>
